import React, { Component } from 'react';

import data from './data';
import './App.css';
import ExtLinkIcon from 'react-icons/lib/fa/external-link';

const { skills, projects, portfolio, refs } = data;

const navTabs = [
  { title: 'Skills', id: 'undermybelt' },
  { title: 'Portfolio', id: 'portfolio' },
  { title: 'Experience', id: 'experience' },
  { title: 'About', id: 'aboutthispage' },
];

class App extends Component {
  state = {
    showFixedHeader: false,
  };

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll = e => {
    const { scrollY } = window;
    if (scrollY > 100) {
      this.setState({ showFixedHeader: true });
    } else {
      this.setState({ showFixedHeader: false });
    }
  };

  render() {
    const { showFixedHeader } = this.state;
    return (
      <div className="App">
        <header className={`App-fixed-header ${showFixedHeader ? 'show' : ''}`}>
          <div className="container">
            <a className="App-title" href="#">
              Edmond Cheng
            </a>
            <div className="App-flex-space" />
            <div className="App-nav-tabs hidden-sm">
              {navTabs.map(tab => (
                <a className="App-nav-tab" key={`nav-tab-${tab.id}`} href={`#${tab.id}`}>
                  {tab.title}
                </a>
              ))}
            </div>
            <a className="App-github-link" href="https://github.com/edmondcang">
              <svg viewBox="0 0 128 128">
                <path fillRule="evenodd" clipRule="evenodd" d="M64 5.103c-33.347 0-60.388 27.035-60.388 60.388 0 26.682 17.303 49.317 41.297 57.303 3.017.56 4.125-1.31 4.125-2.905 0-1.44-.056-6.197-.082-11.243-16.8 3.653-20.345-7.125-20.345-7.125-2.747-6.98-6.705-8.836-6.705-8.836-5.48-3.748.413-3.67.413-3.67 6.063.425 9.257 6.223 9.257 6.223 5.386 9.23 14.127 6.562 17.573 5.02.542-3.903 2.107-6.568 3.834-8.076-13.413-1.525-27.514-6.704-27.514-29.843 0-6.593 2.36-11.98 6.223-16.21-.628-1.52-2.695-7.662.584-15.98 0 0 5.07-1.623 16.61 6.19C53.7 35 58.867 34.327 64 34.304c5.13.023 10.3.694 15.127 2.033 11.526-7.813 16.59-6.19 16.59-6.19 3.287 8.317 1.22 14.46.593 15.98 3.872 4.23 6.215 9.617 6.215 16.21 0 23.194-14.127 28.3-27.574 29.796 2.167 1.874 4.097 5.55 4.097 11.183 0 8.08-.07 14.583-.07 16.572 0 1.607 1.088 3.49 4.148 2.897 23.98-7.994 41.263-30.622 41.263-57.294C124.388 32.14 97.35 5.104 64 5.104z"></path><path d="M26.484 91.806c-.133.3-.605.39-1.035.185-.44-.196-.685-.605-.543-.906.13-.31.603-.395 1.04-.188.44.197.69.61.537.91zm-.743-.55M28.93 94.535c-.287.267-.85.143-1.232-.28-.396-.42-.47-.983-.177-1.254.298-.266.844-.14 1.24.28.394.426.472.984.17 1.255zm-.575-.618M31.312 98.012c-.37.258-.976.017-1.35-.52-.37-.538-.37-1.183.01-1.44.373-.258.97-.025 1.35.507.368.545.368 1.19-.01 1.452zm0 0M34.573 101.373c-.33.365-1.036.267-1.552-.23-.527-.487-.674-1.18-.343-1.544.336-.366 1.045-.264 1.564.23.527.486.686 1.18.333 1.543zm0 0M39.073 103.324c-.147.473-.825.688-1.51.486-.683-.207-1.13-.76-.99-1.238.14-.477.823-.7 1.512-.485.683.206 1.13.756.988 1.237zm0 0M44.016 103.685c.017.498-.563.91-1.28.92-.723.017-1.308-.387-1.315-.877 0-.503.568-.91 1.29-.924.717-.013 1.306.387 1.306.88zm0 0M48.614 102.903c.086.485-.413.984-1.126 1.117-.7.13-1.35-.172-1.44-.653-.086-.498.422-.997 1.122-1.126.714-.123 1.354.17 1.444.663zm0 0"></path>
              </svg>
            </a>
          </div>
        </header>
        <header className="App-header">
          {/* <img src={logo} className="App-logo" alt="logo" /> */}
          <div className="App-github-link-decor" />
          <div className="App-github-link">
            <a href="https://github.com/edmondcang">
              <svg viewBox="0 0 128 128">
                <path fillRule="evenodd" clipRule="evenodd" d="M64 5.103c-33.347 0-60.388 27.035-60.388 60.388 0 26.682 17.303 49.317 41.297 57.303 3.017.56 4.125-1.31 4.125-2.905 0-1.44-.056-6.197-.082-11.243-16.8 3.653-20.345-7.125-20.345-7.125-2.747-6.98-6.705-8.836-6.705-8.836-5.48-3.748.413-3.67.413-3.67 6.063.425 9.257 6.223 9.257 6.223 5.386 9.23 14.127 6.562 17.573 5.02.542-3.903 2.107-6.568 3.834-8.076-13.413-1.525-27.514-6.704-27.514-29.843 0-6.593 2.36-11.98 6.223-16.21-.628-1.52-2.695-7.662.584-15.98 0 0 5.07-1.623 16.61 6.19C53.7 35 58.867 34.327 64 34.304c5.13.023 10.3.694 15.127 2.033 11.526-7.813 16.59-6.19 16.59-6.19 3.287 8.317 1.22 14.46.593 15.98 3.872 4.23 6.215 9.617 6.215 16.21 0 23.194-14.127 28.3-27.574 29.796 2.167 1.874 4.097 5.55 4.097 11.183 0 8.08-.07 14.583-.07 16.572 0 1.607 1.088 3.49 4.148 2.897 23.98-7.994 41.263-30.622 41.263-57.294C124.388 32.14 97.35 5.104 64 5.104z"></path><path d="M26.484 91.806c-.133.3-.605.39-1.035.185-.44-.196-.685-.605-.543-.906.13-.31.603-.395 1.04-.188.44.197.69.61.537.91zm-.743-.55M28.93 94.535c-.287.267-.85.143-1.232-.28-.396-.42-.47-.983-.177-1.254.298-.266.844-.14 1.24.28.394.426.472.984.17 1.255zm-.575-.618M31.312 98.012c-.37.258-.976.017-1.35-.52-.37-.538-.37-1.183.01-1.44.373-.258.97-.025 1.35.507.368.545.368 1.19-.01 1.452zm0 0M34.573 101.373c-.33.365-1.036.267-1.552-.23-.527-.487-.674-1.18-.343-1.544.336-.366 1.045-.264 1.564.23.527.486.686 1.18.333 1.543zm0 0M39.073 103.324c-.147.473-.825.688-1.51.486-.683-.207-1.13-.76-.99-1.238.14-.477.823-.7 1.512-.485.683.206 1.13.756.988 1.237zm0 0M44.016 103.685c.017.498-.563.91-1.28.92-.723.017-1.308-.387-1.315-.877 0-.503.568-.91 1.29-.924.717-.013 1.306.387 1.306.88zm0 0M48.614 102.903c.086.485-.413.984-1.126 1.117-.7.13-1.35-.172-1.44-.653-.086-.498.422-.997 1.122-1.126.714-.123 1.354.17 1.444.663zm0 0"></path>
              </svg>
            </a>
          </div>
          <h1 className="App-title">
            Edmond Cheng
          </h1>
          <p className="App-title-desc">
            full stack developer and programming enthusiast
          </p>
          <div className="App-nav-tabs">
            {navTabs.map(tab => (
              <a className="App-nav-tab" key={`nav-tab-${tab.id}`} href={`#${tab.id}`}>
                {tab.title}
              </a>
            ))}
          </div>
        </header>
        {/* <p className="App-intro">
          To get started, edit <code>src/App.js</code> and save to reload.
        </p> */}
        <div id="undermybelt" className="App-section">
          <div className="container">
            <h2>Under my belt</h2>
            <div className="row">
              {skills.map(item => (
                <div
                  key={`skill-${item.name}`}
                  className="col-xs-6 col-sm-3 col-md-2"
                >
                  <div className="box">
                    <div className="App-skill-item">
                      <div>
                        <svg viewBox="0 0 128 128">
                          <path fill="#444444" d={item.svgpath}></path>
                        </svg>
                      </div>
                      <p className="App-skill-item-name">{item.name}</p>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
        <div id="portfolio" className="App-section even">
          <div className="container">
            <h2>Portfolio</h2>
            {/* <div className="row middle-xs"> */}
            <div className="row">
              {portfolio.map(item => (
                <div
                  key={`port-${item.title}`}
                  className="col-sm-6 col-md-4"
                >
                  <div className="box">
                    <div className="App-port-item">
                      <h4>{item.title}</h4>
                      <div className="App-port-item-screenshot">
                        <img src={item.screenshot} alt={item.title} />
                      </div>
                      <div className="App-port-item-desc">
                        <p>{item.desc}</p>
                      </div>
                      {item.url ? (
                        <p className="App-refs-item-btn">
                          <a href={item.url}>
                            VIEW PROJECT <ExtLinkIcon />
                          </a>
                        </p>
                      ) : ''}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
        <div id="experience" className="App-section">
          <div className="container">
            <h2>What I have made</h2>
            {/* <div className="row middle-xs"> */}
            <div className="row">
              {projects.map(item => (
                <div
                  key={`proj-${item.name}`}
                  className="col-sm-6 col-md-4"
                >
                  <div className="box">
                    <div className="App-proj-item">
                      {item.name}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
        <div id="aboutthispage" className="App-section even">
          <div className="container">
            <h2>About this page</h2>
            <p className="App-refs-title-desc">
              <em>the following cool stuff are used to craft this page</em>
            </p>
            <div className="row middle-xs">
              {refs.map(item => (
                <div
                  key={`proj-${item.name}`}
                  className="col-sm-6"
                >
                  <div className="App-block">
                    <h3 className="App-refs-item-name">
                      {item.name}
                    </h3>
                    <p className="App-refs-item-desc">{item.desc}</p>
                    <p className="App-refs-item-btn">
                      <a href={item.url}>
                        VIEW PROJECT <ExtLinkIcon />
                      </a>
                    </p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;



